.wrapper {
    flex-direction: column;
    text-align: center;
    display: flex;
  }
  .title {
    text-align: center;
    color: rgb(244, 164, 96);
  }
  .input {
    height: 20px;
    text-align: center;
  }
  .button_wrapper {
    text-align: center;
    margin-top: 14px;
  }
  .button {
    border: none;
    border-radius: 3px;
    outline: none;
    cursor: pointer;
    height: 30px;
    width: 110px;
    background-color: rgb(255, 235, 205);
  }
  .title_item {
    color: rgb(139, 69, 19);
  }